.page-header__wrapper--top {
  background: #051F37; }

.page-header__wrapper--btm {
  background: #0E5FAD; }

.page-header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.page-header__link {
  color: #fff;
  -webkit-transition: .4s;
  transition: .4s; }
  .page-header__link:hover {
    color: #FFC808; }
  .page-header__link:active {
    color: #FFC808; }

.page-header__select {
  border: none;
  background: inherit;
  font-size: 14px;
  color: #fff; }

.page-header__tel {
  color: #fff;
  text-transform: uppercase;
  font-family: arial; }
  .page-header__tel:hover {
    color: #FFC808; }
  .page-header__tel:active {
    color: #FFC808; }

.user__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.starbox .star_holder .star {
  float: left; }

.starbox .positioner {
  display: inline; }

.positioner {
  position: relative;
  display: inline-block;
  line-height: 0; }

.starbox .colorbar,
.starbox .ghost {
  z-index: 0;
  position: absolute;
  max-width: 100%;
  top: 0;
  bottom: 0;
  left: 0; }

.starbox .stars {
  display: inline-block; }

.starbox .stars .star_holder {
  position: relative;
  z-index: 1; }

.starbox .stars .star_holder .star {
  display: inline-block;
  vertical-align: baseline;
  background-repeat: no-repeat; }

.starbox .stars .star_holder .star {
  background-image: url("../images/5-large.png");
  width: 33px;
  height: 33px; }

.starbox .stars {
  background: #cccccc; }

.starbox .rated .stars {
  background: #dcdcdc; }

.starbox .rated.hover .stars {
  background: #cccccc; }

.starbox .colorbar {
  background: #1e90ff; }

.starbox .hover .colorbar {
  background: #ffcc1c; }

.starbox .rated .colorbar {
  background: #64b2ff; }

.starbox .rated.hover .colorbar {
  background: #1e90ff; }

.starbox .ghost {
  background: #a1a1a1; }

a:hover {
  text-decoration: none; }

body {
  font-family: arial;
  font-size: 14px; }

@font-face {
  font-family: "Europe";
  src: url("../font/Europe.ttf.woff") format("woff");
  font-weight: normal;
  font-display: swap;
  font-style: normal; }
