

.page-header__wrapper {

  &--top {
    background: $header-top-color;
  }

  &--btm {
    background: $header-btm-color;
  }
}

.page-header__list {
  @include reset-list;
  display: flex;
}

.page-header__link {
  color: #fff;
  transition: .4s;

  &:hover {
    color: #FFC808;
  }

  &:active {
    color: #FFC808;
  }
}

.page-header__select {
  border: none;
  background: inherit;
  font-size: 14px;
  color: #fff;
}

.page-header__tel {
  color: #fff;
  text-transform: uppercase;
  font-family: $type-font;

  &:hover {
    color: #FFC808;
  }

  &:active {
    color: #FFC808;
  }
}
